import React from 'react'

function isZenkaku(str = '') {
  // eslint-disable-next-line
  if (str.match(/^[^\x01-\x7E\xA1-\xDF]+$/)) {
    return true;
  } else {
    return false;
  }
}

const Hankaku = ({value}) => (
  <tspan className="noshi__text noshi__text--hankaku">{value}</tspan>
)

const Zenkaku = ({value}) => (
  <tspan className="noshi__text">{value}</tspan>
)

const Text = props => (
  <text {...props}>
    {[...props.children].map((str, i) => {
      const Tag = isZenkaku(str) ? Zenkaku : Hankaku

      return <Tag key={i} value={str} />
    })}
  </text>
)

export default Text