import React from 'react'
import Text from './Text'
import Images from './Images/index'

const Preview = ({ noshi }) => (
  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 297 210">
    <image xlinkHref={Images[`noshi${noshi.type}`]} x="0" y="0" width="297" height="210" />
    <g className="noshi__omote">
      <Text className="noshi__omotegaki" x="50%" y="20%">
        {noshi.omotegaki}
      </Text>
    </g>
    <g className="noshi__ura">
      {(() => {
        const names = noshi.name.split('\n')
        const margin = 15
        const start = -(names.length - 1) * (margin / 2)
        
        return names.map((name, i) => (
          <Text key={name} className="noshi__name" x="50%" dx={start + (i * margin)} y="130">
            {name}
          </Text>
        ))
      })()}
    </g>
  </svg>
)

export default Preview;